import React from 'react';
import Burger from './Burger';

const Header = (props) => {
	return (
		<header className="header">
			<div>
				<h1
					data-page="Home"
					onClick={props.nav}>
					{
						'FIG'.split('').map((letter, i) => {
							return <div 
											data-page="Home"
											onClick={props.nav}
											key={i}>{letter}</div>
						})
					}
				</h1>
				<Burger 
					classes={props.burgerStuff} 
					burger={props.burger} />
			</div>
		</header>
	);
}

export default Header;


