import React from 'react';

const Menu = (props) => {
	const d = props.classes === "menu" ? 0 : 0.3;
	return (
		<section className={props.classes}>
			<div className="panel panel1"></div>
			<div className="panel panel2"></div>
			<div className='links'>
				<div 
					className={props.page === 'Home' ? 'link on-page' : 'link'}
					onTouchStart={e => e.target.classList.add('active')}
					onTouchEnd={e => e.target.classList.remove('active')}
					onClick={props.nav}  
					data-page="Home">
					{
						'Home'.split('').map((letter, i) => {
							return <div
												key={i}
												className='link-letter'
												style={{
													transitionDelay: (d + (i/20)) + 's'
												}}>{letter}</div>
						})
					}
					<div className='inner'>
						{
							'Home'.split('').map((letter, i) => {
								return <div
													key={i}
													style={{
														transitionDelay: (d + (i/20)) + 's'
													}}>{letter}</div>
							})
						}
					</div>
				</div>
				<div 
					className={props.page === 'Work' ? 'link on-page' : 'link'}
					onTouchStart={e => e.target.classList.add('active')}
					onTouchEnd={e => e.target.classList.remove('active')}
					onClick={props.nav}
					data-page="Work">
					{
						'Work'.split('').map((letter, i) => {
							return <div
												key={i} 
												className='link-letter'
												style={{
													transitionDelay: (d + (i/20)) + 's'
												}}>{letter}</div>
						})
					}
					<div className='inner'>
						{
							'Work'.split('').map((letter, i) => {
								return <div
													key={i}
													style={{
														transitionDelay: (d + (i/20)) + 's'
													}}>{letter}</div>
							})
						}
					</div>
				</div>
				<div 
					className={props.page === 'Contact' ? 'link on-page' : 'link'}
					onTouchStart={e => e.target.classList.add('active')}
					onTouchEnd={e => e.target.classList.remove('active')}
					onClick={props.nav}
					data-page="Contact">
					{
						'Contact'.split('').map((letter, i) => {
							return <div
												key={i}
												className='link-letter'
												style={{
													transitionDelay: (d + (i/20)) + 's'
												}}>{letter}</div>
						})
					}
					<div className='inner'>
						{
							'Contact'.split('').map((letter, i) => {
								return <div
													key={i}
													style={{
														transitionDelay: (d + (i/20)) + 's'
													}}>{letter}</div>
							})
						}
					</div>
				</div>
			</div>
		</section>
	);
}

export default Menu;


